import React, { Component } from 'react';
import './App.css';


class App extends Component {
  constructor(props) {
    super(props);
    this.displayMsg = this.displayMsg.bind(this);
   }

  displayMsg = () => {
    return 'Hello World...!';
  }

  render() {
    return (
      <div className="App">
        <br/><br/><br/>
        <Hello/>
      </div>
    );
  }
}

class Hello extends Component {
  render(){
    const msg = App.displayMsg();

    return(
      <div>
        <h2>{msg}</h2>
      </div>
    );
  }
}


export default App;

